#employee-div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  font-size: 25px;
 
}

.path {  
  font-family: "Dancing Script", cursive;
  
  color:black;
  font-size: 25px;
 
  
}

#employee-div h3 {
  font-size: 25px;
  margin-top: 0px;
}



table {
  font-size: 13px;
  border-collapse: collapse;
  width: 100%;
  margin-top: 10px;
  color:black;
}

table td,
table th {
  border: 1px solid #ddd;
  padding: 8px;
}


table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: black;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  color: white;
}

.footerTable {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 40px;
  color: black;
}

.footerTable button {
  background: rgb(134, 150, 156);
  padding: 5px 15px;
  border: white;
  border-radius: 5px;
  margin-left: 5px;
  color: white;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}


.search {
  margin-top: 50px;
  color: rgb(15, 15, 15);
}

